.Robot {
  display: -ms-flexbox;
  display: flex;
  padding: 1em;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 20px;
  border: 5px inset;
  border-radius: 10%;
  -webkit-animation-name: fight;
          animation-name: fight;
  -webkit-animation-duration: 15s;
          animation-duration: 15s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: forwards;
          animation-direction: forwards;
  -webkit-animation-timing-function: flat;
          animation-timing-function: flat;
}

@-webkit-keyframes fight {
  0% {
    border-color: white;
  }
  20% {
    border-color: #ff00b2;
  }
  40% {
    border-color: #ff0040;
  }
  60% {
    border-color: #ff9100;
  }
  80% {
    border-color: #9dff00;
  }
  95% {
    border-color: #00ffbf;
  }
  100% {
    border-color: white;
  }
}

@keyframes fight {
  0% {
    border-color: white;
  }
  20% {
    border-color: #ff00b2;
  }
  40% {
    border-color: #ff0040;
  }
  60% {
    border-color: #ff9100;
  }
  80% {
    border-color: #9dff00;
  }
  95% {
    border-color: #00ffbf;
  }
  100% {
    border-color: white;
  }
}

.Robot h3 {
  color: white;
  font-size: 40px;
}



